<template>
  <div class="visualizer">
    <h1>Kinesis Example</h1>


    <kinesis-container :audio="audio" :playAudio="isPlaying" crossOrigin="anonymous">
      <button
          class="button"
          :class="isPlaying && 'button--playing'"
          @click="changeState"
      >hey</button>

      <audio controls>
        <source src="static/temp.mp3" />
      </audio>

      <kinesis-audio :audioIndex="10" crossOrigin="anonymous">


        <kinesis-element :strength="10">
          <svg xmlns="http://www.w3.org/2000/svg" height="80" viewBox="0 0 24 24" width="80">
            <path d="M20 18v-1c1.1 0 1.99-.9 1.99-2L22 5c0-1.1-.9-2-2-2H4c-1.1 0-2 .9-2 2v10c0 1.1.9 2 2 2v1H1c-.55 0-1 .45-1 1s.45 1 1 1h22c.55 0 1-.45 1-1s-.45-1-1-1h-3zM5 5h14c.55 0 1 .45 1 1v8c0 .55-.45 1-1 1H5c-.55 0-1-.45-1-1V6c0-.55.45-1 1-1z"></path>
          </svg>
        </kinesis-element>


      </kinesis-audio>



    </kinesis-container>


  </div>
</template>

<script crossorigin="anonymous">
import { KinesisContainer, KinesisElement } from 'vue-kinesis'

export default {
  components: {
  },
  data() {
    return {
      audio: '/static/temp.mp3',
      isPlaying: false
    }
  },
  methods: {
    changeState() {
      this.isPlaying = !this.isPlaying
    }
  }
}
</script>


